import React from 'react';
import TextLoop from 'react-text-loop';
import Fade from 'react-reveal/Fade';

const wordList = [
  { item: 'a duke fuqua grad', emoji: '🚀' },
  { item: 'a data scientist', emoji: '☕' },
  { item: 'a social worker', emoji: '🥰' },
  { item: 'an avid data storyteller', emoji: '🧪' },
];

const Typewriter = () => {
  return (
    <div>
      <Fade bottom delay={750}>
        <h3 className='subtitle has-text-weight-light  is-size-4  is-size-5-mobile  has-text-white'>
          and I'm{' '}
          <TextLoop adjustingSpeed={100} delay={1700} noWrap={true}>
            {wordList.map(({ item, emoji }) => {
              return (
                <span
                  key={Math.random()}
                  className=' has-text-weight-bold is-size-4  is-size-6-mobile has-text-white'
                >
                  {item}
                  <span role='img' aria-label={item}>
                    {' '}
                    {emoji}
                  </span>
                </span>
              );
            })}
          </TextLoop>
        </h3>
      </Fade>
    </div>
  );
};
export default Typewriter;
