import React, { Component } from 'react';
import Slider from 'react-slick';
import Card from './Cards.js';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import PCjpg from '../Assets/Images/PC.jpg';
import PCwebp from '../Assets/Images/PC.webp';
import CPjpg from '../Assets/Images/CP.jpg';
import CPwebp from '../Assets/Images/CP.webp';
import EMjpg from '../Assets/Images/EM.jpg';
import EMwebp from '../Assets/Images/EM.webp';
import DLwebp from '../Assets/Images/DL.webp';
import DLjpg from '../Assets/Images/DL.jpg';
import PFwebp from '../Assets/Images/PF.webp';
import PFjpg from '../Assets/Images/PF.jpg';
import MNMLjpg from '../Assets/Images/MNML.jpg';
import MNMLwebp from '../Assets/Images/MNML.webp';

class CardCarousel extends Component {
  render() {
    let settings = {
      dots: true,
      infinite: true,
      slidesToShow: 2,
      slidesToScroll: 1,
      lazyLoad: true,
      fade: false,
      initialSlide: 2,
      swipeToSlide: true,
      pauseOnHover: true,
      autoplay: false,
      focusOnSelect: true,
      speed: 500,
      autoplaySpeed: 2000,
      centerPadding: '60px',
      centerMode: true,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
            centerMode: false,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: false,
          },
        },
      ],
    };
    return (
      <div className='container  mt-3'>
        <Slider {...settings}>
          <Card
            index={0}
            color='warning'
            title='Dognition Database Analytics'
            subtitle='SQL, Jupyter Notebook'
            imgSrc={[CPjpg, CPwebp]}
            content={[
              'Drove analysis and data cleansing for a large data set and examined 10K+ data points in complex relational data structures on MySQL Workbench; identified existing issues and errors',
              'Conducted financial statement analysis and created budget forecast models to generate key insights about users, and revenue growth opportunities.',
            ]}
          />
          <Card
            index={1}
            color='success'
            title='Forecasting FIFA soccer players’ effective allocation in teams'
            subtitle='Kaggle'
            imgSrc={[EMjpg, EMwebp]}
            content={[
              'Used unsupervised and supervised machine learning models to effectively allocate players in teams based on present and future values. ',
              'Conducted post-lasso and 10-fold cross validation post-analysis.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/Employee-Management-Android-Port',
              'https://drive.google.com/file/d/1m-S7Ow2FUCIpIxzGnItm4lIOQvnris4I/view',
            ]}
          />
          <Card
            index={2}
            color='light'
            title='Analyzing Crime in Cary NC'
            subtitle='Tableau, SQL'
            imgSrc={[PCjpg, PCwebp]}
            content={[
              'Created a data-driven model to formulate questions to visualize and analyze incidence of crimes in a public crime data set.',
              'Built models to ascertain duration, location, and external factors impacting crimes.',
              'Designed dashboard to trends of key metrics for mitigating crimes.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/Pomodoro-Clock-Source-Code',
              'https://srijansrivastava.tech/PomodoroClockHello/',
            ]}
          />
          <Card
            index={3}
            color='danger'
            title='Diamond Prices Prediction'
            subtitle='R, SQL'
            imgSrc={[DLjpg, DLwebp]}
            content={[
              'Led analysis and data cleansing on prices and quality of diamonds using exploratory data analysis, hypothesis testing, regression and classification.',
              ' Created a predictive matrix for foreseeing changes that could affect buying and selling behavior in the market.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/Data-Analytics-Viz-Projects/blob/master/911-Calls-Project/01-911%20Calls%20Data%20Capstone%20Project.ipynb',
            ]}
          />
          <Card
            index={5}
            color='primary'
            title='Superstore Regional Sales Analysis'
            subtitle='Tableau'
            imgSrc={[PFjpg, PFwebp]}
            content={[
              'Designed compelling dashboards to visualize the performance of product categories in different regions in North America to identify key problem areas and success factors in-store profitability.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/Portfolio-Source-Code',
            ]}
          />
          <Card
            index={5}
            color='warning'
            title='Major League Baseball Analytics '
            subtitle='SQL '
            imgSrc={[MNMLjpg, MNMLwebp]}
            content={[
              'Queried database containing pitching, hitting, and fielding statistics for Major League Baseball.',
              'Led analysis to examine the trend of ranking and team dynamics for the Atlantic Braves, and compared their performance over the years.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/MNMLStore',
              'https://mnml.srijansrivastava.tech/',
            ]}
          />
          <Card
            index={6}
            color='info'
            title='Fraud TBB Analysis'
            subtitle='Excel, Tableau'
            imgSrc={[MNMLjpg, MNMLwebp]}
            content={[
              'Performed risk-based internal audit for TBB’s PCard program, identifying risk and creating an audit plan. ',
              'Evaluated operating effectiveness using risk analysis, budget forecast models and Tableau to prepare an audit report with primary findings and recommendations.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/MNMLStore',
              'https://mnml.srijansrivastava.tech/',
            ]}
          />
          <Card
            index={7}
            color='light'
            title='Google Forensics Hackathon '
            subtitle='SQL'
            imgSrc={[MNMLjpg, MNMLwebp]}
            content={[
              'Led a study & co-authored Market Intelligence research finding for Google Technical Infrastructure on estimating the scale of Amazon’s server operation based on SEC filings, earnings statements, industry reports, and other publicly accessible information.',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/MNMLStore',
              'https://mnml.srijansrivastava.tech/',
            ]}
          />
          <Card
            index={8}
            color='info'
            title='Jabil Supply Chain Optimization'
            subtitle='R, Python, Tableau'
            imgSrc={[MNMLjpg, MNMLwebp]}
            content={[
              'Led a team of 7 and optimized Jabil’s supply chain & inventory management system.',
              'Provided recommendations on 50K + data points by estimating movement and cost of holding inventory, created dashboards to monitor specific plants, identified gaps in data, and recognized terms of contracts to avoid to mitigate existing risks',
            ]}
            links={[
              'https://github.com/SrivastavaSrijan/MNMLStore',
              'https://mnml.srijansrivastava.tech/',
            ]}
          />
        </Slider>
      </div>
    );
  }
}

export default CardCarousel;
