import React from 'react';
//import AboutMyFoot from '../Components/AboutMyFoot.js';
import MEwebp from '../Assets/Images/Me.webp';
import MEjpg from '../Assets/Images/Me.png';
import Headings from '../Components/Headings.js';
import Image from '../Components/Image.js';
import Fade from 'react-reveal/Fade';

export const AboutMe = () => {
  return (
    <section className='section' id='about'>
      <Headings number='01.' title='Me' emoji='👀' />
      <div className='card '>
        <div className='card-content is-paddingless '>
          <div className='is-muted-link px-6 pt-5 pb-3'>
            <Fade delay={500}>
              <h2 className='title is-size-3 is-size-4-mobile'>
                Hey! I'm Shruthi.
                <span role='img' aria-label={'smile'}>
                  {' '}
                  😄
                </span>
                <br />
              </h2>

              <h3 className='subtitle is-family-secondary mb-4 is-size-4  is-size-5-mobile'>
                I'm building new experiences.
              </h3>
            </Fade>
          </div>

          <div className='level'>
            <div className='level-left '>
              <Fade left delay={700}>
                <div className='image is-1by1 is-avatar ml-2 mr-4 my-4 '>
                  <Image
                    imgJpg={MEjpg}
                    imgWebp={MEwebp}
                    alt='Picture of Srijan'
                  />
                </div>
              </Fade>
            </div>
            <Fade delay={300}>
              <div
                className='level-content px-6 py-3 '
                style={{ alignSelf: 'middle', lineHeight: 1.5 }}
              >
                <p className='is-family-secondary is-size-5 is-size-7-mobile'>
                  I'm a <span className='bold-danger'>2020</span> pass out of
                  the{' '}
                  <span className='bold-link'>
                    Fuqua School of Business, Duke University
                  </span>
                  {', '}
                  where I graduated in{' '}
                  <span className='bold-primary'>Data Analytics</span> I am
                  interested in working with
                  <span className='bold-success'>
                    tech-intensive startups{' '}
                  </span>{' '}
                  which would allow me to rotate between departments and hone my
                  technical abilities. After gaining experience, I would
                  eventually like to work with an organization with a proclivity
                  for social development.
                </p>
              </div>
            </Fade>
          </div>
        </div>
      </div>
    </section>
  );
};
