import React from 'react';
import { VerticalTimeline } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import { BarChart, BarChart2, DollarSign, Book, Heart } from 'react-feather';
import { TimelineElement } from './TimelineElement';
const experienceArray = [
  {
    title: 'Bootstrap Advisors',
    subtitle: 'Data Analyst Intern',
    description: [
      'Performed descriptive & diagnostic market analysis of large datasets from a product incubator startups multi-million-dollar portfolio, Happy Wax, and maximized growth in over 2K retail locations.',
      'Drove customer success and business operation for different product categories.',
    ],
    date: 'Spring 2020',
    color: 'light',
    icon: <BarChart2 />,
  },
  {
    title: 'Recity, Duke Interdisciplinary Social Innovators',
    subtitle: 'Consultant',
    description: [
      'Conducted social network analysis & designed surveys; Led data collection, behavioral analytics resulting in identification of synergies, high order exploration, thereby increased collaboration across 50+ clients.',
      'Identified risks and inefficiencies to enhance and amplify organizational collaboration.',
    ],
    date: 'Spring 2020',
    color: 'primary',
    icon: <BarChart />,
  },
  {
    title: 'Doctors without Borders',
    subtitle: 'Finance Intern',
    description: [
      'Remediated refugee crisis with an NPO project initiation by preparing a memorandum of exemptions.',
      'Created a user- friendly ERP manual enabling easier process adoption for global distribution activities.',
      'Streamlined General Director of Finance’s bookkeeping and spend management by creating a regulatory framework.',
      'Enabled quarterly reconciliation by creating Budget Control Reports for MSF’s global offices.',
    ],
    date: 'Spring 2020',
    color: 'warning',
    icon: <DollarSign />,
  },
  {
    title: 'Mankind Pharma Ltd.',
    subtitle: 'Legal Intern ',
    description: [
      'Researched and identified compliance requirements under the (Indian) Companies Act.',
      'Shadowed the legal counsel, facilitated conflict management and resolution.',
    ],
    date: 'Spring 2020',
    color: 'info',
    icon: <Book />,
  },
  {
    title: 'Enactus',
    subtitle: 'Data Analyst Intern',
    description: [
      'Executive Member – Developed sales strategy, renegotiated supply contracts, and coordinated skill development classes. Improved the livelihood of rural artisans by expanding market stall in 2 zones, raising awareness through social media increasing craft sales by 75%.',
      'Project Head – Improved sustainable housing, safety, and health of the impoverished sectors of Delhi with an in-depth statistical exploration, analysis, and recommendation of socio-economic and geopolitical factors.',
    ],
    date: 'Spring 2020',
    color: 'danger',
    icon: <Heart />,
  },
];
export const Timeline = () => {
  return (
    <VerticalTimeline>
      {experienceArray.map(({ title, subtitle, description, color, icon }) => (
        <TimelineElement
          title={title}
          subtitle={subtitle}
          description={description}
          color={color}
          icon={icon}
        />
      ))}
    </VerticalTimeline>
  );
};
